import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { selectScirt } from './../../actions';

import ScirtSelection from './../presentational/ScirtSelection';

class ScirtSelectionContainer extends Component {
    render() {
        return (
            this.props.scirts.map((scirt) => (
                <ScirtSelection 
                    key={scirt.id}
                    isSelected={scirt.isSelected}     
                    image={scirt.image}
                    handleClick={() => this.props.selectScirt(scirt.id)}
                />               
            ))              
        )
    }
}

ScirtSelectionContainer.propTypes = {
    scirts: PropTypes.array.isRequired,
    selectScirt: PropTypes.func.isRequired,
}

export default connect(
    state => ({ scirts: state.scirts }),
    { selectScirt }
    // (dispatch, ownprops) => ({
    //     selectScirt: () => dispatch(selectScirt(ownprops.scirts.id)),
    // }),
)(ScirtSelectionContainer);


