import React, { Component } from 'react';

import IntroductionHeader from './../presentational/IntroductionHeader';
import GameSetting from './../presentational/GameSetting';
import UserSetting from './../presentational/UserSetting';

import './../../styles/Introduction.css';

class IntroductionContainer extends Component {
    render() {
        return (
            <section className='introduction'>
                <IntroductionHeader />
                <GameSetting />
                <UserSetting />
            </section>
        )
    }
}

export default IntroductionContainer;


