import React from 'react';

import ScirtSelectionContainer from '../container/ScirtSelectionContainer';

import './../../styles/GameSetting.css';

const GameSetting = () => {
    return (
        <section className='game-setting'>
            <h2>Game Setting</h2>
            <figure className='skirt-cards'>
                <figcaption>Select Scirt:</figcaption>
                <div>
                    <ScirtSelectionContainer />
                </div>
            </figure>
        </section>
    )
}

export default GameSetting;


