import {
  FETCH_RECORDS_SUCCESS,
  FETCH_RECORDS_ERROR,
  FETCH_RECORDS_REQUEST,
  SORT_RESULTS,
  DELETE_RESULTS
} from './../constants';

const initialState = {
  data: null,
  isLoading: false,
  isError: false,
  error: null,
  isSorted: false
};

const results = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_RECORDS_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case FETCH_RECORDS_SUCCESS:
      return {
        ...state,
        data: action.payload,
        isLoading: false
      };
    case FETCH_RECORDS_ERROR:
      return {
        ...state,
        isLoading: false,
        isError: true,
        error: action.payload
      };
    case SORT_RESULTS:
      return action.payload !== null
        ? {
            ...state,
            data: action.payload.sort((a, b) => a.score - b.score),
            isSorted: true
          }
        : {
            ...state,
            data: [],
            isSorted: true
          };
    case DELETE_RESULTS:
      return {
        ...state,
        data: null,
        isSorted: false,
        isError: false,
        error: null
      };
    default:
      return state;
  }
};

export default results;



