import { SEND_DATA_ERROR } from './../constants';

const initialState = {
    isError: false,
    error: null,
}

const userData = (state = initialState, action) => {
    switch (action.type) {
        case SEND_DATA_ERROR:
            return { 
                ...state, 
                isError: true, 
                error: action.payload 
            };
        default:
            return state;
    }
}

export default userData;


