import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { connect } from 'react-redux';
import { scirtSelector } from '../../selectors';

import './../../styles/Card.css';

const Card = ({isTurned, isOut, scirt, image, handleClick}) => {
    const cardClass = classNames({
        'card': !isTurned,
        'turned-card': isTurned,
        'retired-card': isOut,
    }); 
    
    return (
        <div  
            className={cardClass}
            onClick={handleClick}
        >   
            <img 
                src={isTurned ? image : scirt}
            />
        </div>
    )
}

Card.propTypes = {
    isTurned: PropTypes.bool.isRequired,
    isOut: PropTypes.bool.isRequired,
    scirt: PropTypes.string.isRequired,
    image: PropTypes.string.isRequired,
    handleClick: PropTypes.func.isRequired,
}

export default connect(
    scirtSelector,
)(Card);



