import React, { Component } from 'react';
import PropTypes from 'prop-types';
//import { createSelector } from 'reselect';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { initCards, turnCard, checkCard, checkGame } from './../../actions';

import Card from './../presentational/Card';

import './../../styles/GameField.css';

class GameFieldContainer extends Component {
  componentWillMount() {
    this.props.initCards();
  }

  async componentDidUpdate() {
    await this.props.checkGame(this.props.cards);
    this.props.isFinished && this.props.history.push('/results');
  }

  handleClick = async id => {
    this.props.turnCard(id);
    await this.props.checkCard();
  };

  render() {
    const { cards } = this.props;

    return (
      <section className="game-field-container">
        {cards.map(card => (
          <Card
            key={card.id}
            isTurned={card.isTurned}
            isOut={card.isOut}
            image={card.image}
            handleClick={this.handleClick.bind(null, card.id)}
          />
        ))}
      </section>
    );
  }
}

GameFieldContainer.propTypes = {
  cards: PropTypes.array.isRequired,
  isFinished: PropTypes.bool.isRequired,
  initCards: PropTypes.func.isRequired,
  turnCard: PropTypes.func.isRequired,
  checkCard: PropTypes.func.isRequired,
  checkGame: PropTypes.func.isRequired
};

export default withRouter(
  connect(
    state => ({
      cards: state.cards,
      isFinished: state.gameState.isFinished
    }),
    {
      initCards,
      checkGame,
      turnCard,
      checkCard
    }
  )(GameFieldContainer)
);



