import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import ScorePageHeader from './../presentational/ScorePageHeader';

class ScorePageHeaderContainer extends Component {
    render() {
        return (
            <ScorePageHeader 
                score={this.props.score}
            />
        )
    }
}

ScorePageHeaderContainer.propTypes = {
    score: PropTypes.number,
}

export default connect(
    state => ({ score: state.gameState.score })
)(ScorePageHeaderContainer)


