import React from 'react';
import PropTypes from 'prop-types';

import './../../styles/ScorePageHeader.css';

const ScorePageHeader = ({score}) => {
    return (
        <p>
            <span>
                Congratulations!!!
            </span>
            <br/>
            Your score:  
            <span>
                {score}
            </span>
            !
        </p>
    )
}

ScorePageHeader.propTypes = {
    score: PropTypes.number,
}

export default ScorePageHeader;


