import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { 
    setScore, 
    sendData, 
    fetchRecords, 
    sortResults, 
    deleteResults, 
} from './../../actions';

import ScorePageHeaderContainer from './../container/ScorePageHeaderContainer';
import RecordsTableContainer from './../container/RecordsTableContainer';
import ScorePageButtonsContainer from './../container/ScorePageButtonsContainer';

import './../../styles/ScorePage.css';

class ScorePageContainer extends Component {
    async componentWillMount() {
        await this.props.setScore(this.props.time);
        await this.props.sendData(
            this.props.userData.username,
            this.props.userData.email,
            this.props.score,
        );
        await this.props.fetchRecords();
        await this.props.sortResults(this.props.results.data);
    }

    componentWillUnmount() {
        this.props.deleteResults();
    }

    render() {    
        return !this.props.results.isSorted
            ? <p>...Loading</p>
            : <section className='user-result'>
                <ScorePageHeaderContainer />
                <ScorePageButtonsContainer />
                <RecordsTableContainer />
            </section>
    }
}

ScorePageContainer.propTypes = {
    results: PropTypes.object.isRequired,
    time: PropTypes.string.isRequired,
    userData: PropTypes.object.isRequired,
    score: PropTypes.number,
    setScore: PropTypes.func.isRequired,
    sendData: PropTypes.func.isRequired,
    fetchRecords: PropTypes.func.isRequired,
    sortResults: PropTypes.func.isRequired,
    deleteResults: PropTypes.func.isRequired,
}

export default connect(
    state => ({ 
        results: state.results,
        time: state.timer.time,
        userData: state.form.registration.values,
        score: state.gameState.score,
    }),
    {
        setScore,
        sendData,
        fetchRecords,
        sortResults,
        deleteResults,
    },
)(ScorePageContainer);


