import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import RecordsTableRow from './../presentational/RecordsTableRow';

import './../../styles/RecordsTable.css';

class RecordsTableContainer extends Component {
  render() {
    //const { results } = this.props.results.data;

    return (
      <table className="table-records">
        <thead>
          <tr>
            <th>User Name</th>
            <th>Email</th>
            <th>Score</th>
          </tr>
        </thead>
        <tbody>
          {this.props.results.length
            ? this.props.results.map(result => (
                <RecordsTableRow
                  key={result._id}
                  username={result.username}
                  email={result.email}
                  score={result.score}
                />
              ))
            : null}
        </tbody>
      </table>
    );
  }
}

RecordsTableContainer.propTypes = {
  results: PropTypes.array.isRequired
};

export default connect(state => ({ results: state.results.data }))(
  RecordsTableContainer
);



